import request from '@/utils/request'

/* 获取最新红线拐点 */
export const gdShp = data => request({ url: '/ygjc/image/gdShp', method: 'post', data })
/* 下载红线拐点 */
export const gdFile = data => request({ url: '/ygjc/image/gdFile', method: 'post', data })
/* 获取最新拐点 */
export const getGdFile = data => request({ url: '/ygjc/image/getGdFile', method: 'post', data })
/* 保存拐点 */
export const saveShp = data => request({ url: '/ygjc/image/addGdShp', method: 'post', data })
